const translateOperator = {
  eq: "==",
  gt: ">",
  gte: ">=",
  lt: "<",
  lte: "<=",
  neq: "!=",
};

function generateFilterMap(filter) {
  return Object.keys(filter).map((key) => {
    const operatorName = Object.keys(filter[key])[0];
    return {
      property: key,
      operator: translateOperator[operatorName],
      value: filter[key][operatorName],
    };
  });
}

function generateQuery(collection, allFilters) {
  // TODO: How do we chain filters on the collections dynamically?
  const filterMap = allFilters.AND.reduce((acc, filter) => {
    return [...acc, ...generateFilterMap(filter)];
  }, []);

  return filterMap.reduce((collectionAcc, filter) => {
    return collectionAcc.where(filter.property, filter.operator, filter.value);
  }, collection);
}

const resolvers = {
  Query: {
    me: async (root, args, ctx) => {
      try {
        const user = await ctx.db
          .collection("users")
          .doc(ctx.auth.currentUser.uid)
          .get();
        return { id: ctx.auth.currentUser.uid, ...user.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    user: async (root, args, ctx) => {
      try {
        const user = await ctx.db.collection("users").doc(args.id).get();
        return { id: args.id, ...user.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    place: async (root, args, ctx) => {
      try {
        const place = await ctx.db.collection("places").doc(args.id).get();
        return { id: args.id, ...place.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    seeds: async (root, args, ctx) => {
      try {
        const seeds = [];
        const seedPromises = [];
        const placesRef = generateQuery(
          ctx.db.collection("places"),
          args.filter
        );

        const places = await placesRef.get();

        places.forEach((place) => {
          seedPromises.push(
            ctx.db
              .collection("seeds")
              .where("isPublic", "==", true)
              .where("placeId", "==", place.id)
              .get()
          );
        });

        const placesWithSeeds = await Promise.all(seedPromises);

        placesWithSeeds.forEach((seedsSnap) => {
          seedsSnap.forEach((seed) => {
            seeds.push({ id: seed.id, ...seed.data() });
          });
        });

        return seeds;
      } catch (e) {
        return new Error(e);
      }
    },
  },
  Mutation: {
    signIn: async (root, args, ctx) => {
      try {
        const { user } = await ctx.auth.signInWithEmailAndPassword(
          args.input.email,
          args.input.password
        );
        const userRef = await ctx.db.collection("users").doc(user.uid).get();
        return { id: user.uid, ...userRef.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    signUp: async (root, args, ctx) => {
      try {
        const { user } = await ctx.auth.createUserWithEmailAndPassword(
          args.input.email,
          args.input.password
        );
        await ctx.db
          .collection("users")
          .doc(user.uid)
          .set({ email: args.input.email });
        return { id: user.uid };
      } catch (e) {
        return new Error(e);
      }
    },
    addPlace: async (root, args, ctx) => {
      try {
        var docRef = await ctx.db.collection("places").add({
          ...args.input,
        });
        return { id: docRef.id, ...args.input };
      } catch (e) {
        return new Error(e);
      }
    },
    updatePlace: async (root, args, ctx) => {
      try {
        await ctx.db
          .collection("places")
          .doc(args.id)
          .update({ ...args.patch });
        const seedRef = await ctx.db.collection("places").doc(args.id).get();
        return { id: seedRef.id, ...seedRef.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    removePlace: async (root, args, ctx) => {
      try {
        await ctx.db.collection("places").doc(args.id).delete();
        return { id: args.id };
      } catch (e) {
        return new Error(e);
      }
    },
    addSeed: async (root, args, ctx) => {
      try {
        var docRef = await ctx.db.collection("seeds").add({
          ...args.input,
        });
        return { id: docRef.id, ...args.input };
      } catch (e) {
        return new Error(e);
      }
    },
    updateSeed: async (root, args, ctx) => {
      try {
        await ctx.db
          .collection("seeds")
          .doc(args.id)
          .update({ ...args.patch });
        const seedRef = await ctx.db.collection("seeds").doc(args.id).get();
        return { id: seedRef.id, ...seedRef.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    removeSeed: async (root, args, ctx) => {
      try {
        await ctx.db.collection("seeds").doc(args.id).delete();
        return { id: args.id };
      } catch (e) {
        return new Error(e);
      }
    },
    addSeedBookmark: async (root, args, ctx) => {
      try {
        await ctx.db
          .collection("users")
          .doc(ctx.auth.currentUser.uid)
          .set(
            {
              bookmarkedSeeds: ctx.firestore.FieldValue.arrayUnion(args.id),
            },
            { merge: true }
          );

        const seed = await ctx.db.collection("seeds").doc(args.id).get();

        return { id: seed.id, ...seed.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    removeSeedBookmark: async (root, args, ctx) => {
      try {
        await ctx.db
          .collection("users")
          .doc(ctx.auth.currentUser.uid)
          .set(
            {
              bookmarkedSeeds: ctx.firestore.FieldValue.arrayRemove(args.id),
            },
            { merge: true }
          );

        const seed = await ctx.db.collection("seeds").doc(args.id).get();

        return { id: seed.id, ...seed.data() };
      } catch (e) {
        return new Error(e);
      }
    },
    updateUser: async (root, args, ctx) => {
      try {
        await ctx.db
          .collection("users")
          .doc(args.id)
          .set({ ...args.patch }, { merge: true });
        const userRef = await ctx.db.collection("users").doc(args.id).get();
        return { id: args.id, ...userRef.data() };
      } catch (e) {
        return new Error(e);
      }
    },
  },
  Seed: {
    place: async (seed, args, ctx) => {
      try {
        if (!seed.placeId) return null;
        const item = await ctx.db.collection("places").doc(seed.placeId).get();
        // TODO: Why do we get item.id even if the place does not exist anymore
        if (!item.data()) return null;
        return { id: item.id, ...item.data() };
      } catch (e) {
        return new Error(e);
      }
    },
  },
  Place: {
    seeds: async (place, args, ctx) => {
      try {
        const items = [];
        const itemsRef = await ctx.db
          .collection("seeds")
          .where("placeId", "==", place.id)
          .get();
        itemsRef.forEach((item) => {
          items.push({ id: item.id, ...item.data() });
        });
        return items;
      } catch (e) {
        return new Error(e);
      }
    },
  },
  User: {
    seeds: async (user, args, ctx) => {
      try {
        const items = [];
        const itemsRef = await ctx.db
          .collection("seeds")
          .where("userId", "==", user.id)
          .get();
        itemsRef.forEach((item) => {
          items.push({ id: item.id, ...item.data() });
        });
        return items;
      } catch (e) {
        return new Error(e);
      }
    },
    places: async (user, args, ctx) => {
      try {
        const items = [];
        const itemsRef = await ctx.db
          .collection("places")
          .where("userId", "==", user.id)
          .get();
        itemsRef.forEach((item) => {
          items.push({ id: item.id, ...item.data() });
        });
        return items;
      } catch (e) {
        return new Error(e);
      }
    },
    bookmarkedSeeds: async (user, args, ctx) => {
      try {
        const promises = [];
        const items = [];
        user.bookmarkedSeeds.forEach((id) => {
          promises.push(ctx.db.collection("seeds").doc(id).get());
        });

        const seeds = await Promise.all(promises);

        seeds.forEach((item) => {
          items.push({ id: item.id, ...item.data() });
        });

        return items;
      } catch (e) {
        return new Error(e);
      }
    },
  },
};

export default resolvers;
